.card {
  background-color: #ffffff;
  border-radius: 16px;
  box-shadow: 0px 4px 6px -1px rgba(0, 0, 0, 0.1), 0px 2px 4px -2px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  transition: box-shadow 0.2s, transform 0.2s;
  cursor: pointer;
}

.card:hover {
  box-shadow: 0px 10px 15px -3px rgba(0, 0, 0, 0.1), 0px 4px 6px -2px rgba(0, 0, 0, 0.1);
  transform: translateY(-2px);
}

.imageContainer {
  position: relative;
  height: 224px;
  overflow: hidden;
}

.image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center 50%;
}

.favoriteButton {
  position: absolute;
  top: 12px;
  right: 12px;
  width: 36px;
  height: 36px;
  background-color: rgba(255, 255, 255, 0.9);
  border: none;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: background-color 0.2s, transform 0.2s;
}

.favoriteButton:hover {
  background-color: #ffffff;
  transform: scale(1.1);
}

.favoriteButton svg {
  color: #6b7280;
}

.badges {
  position: absolute;
  top: 12px;
  left: 12px;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.badge {
  background-color: #155dfc;
  color: #ffffff;
  font-family: 'Inter', 'Noto Sans JP', 'Noto Sans SC', sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 16px;
  padding: 4px 12px;
  border-radius: 4px;
  white-space: nowrap;
}

.content {
  padding: 20px;
  height: 278px;
  display: flex;
  flex-direction: column;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 8px;
}

.name {
  font-family: 'Inter', 'Noto Sans JP', 'Noto Sans SC', sans-serif;
  font-size: 18px;
  font-weight: 600;
  line-height: 28px;
  color: #101828;
  letter-spacing: -0.4395px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  flex: 1;
}

.rating {
  display: flex;
  align-items: center;
  gap: 4px;
  flex-shrink: 0;
}

.ratingValue {
  font-family: 'Inter', sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
  color: #101828;
  letter-spacing: -0.1504px;
}

.reviewCount {
  font-family: 'Inter', sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  color: #6a7282;
  letter-spacing: -0.1504px;
}

.location {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 16px;
  font-family: 'Inter', 'Noto Sans JP', sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  color: #4a5565;
  letter-spacing: -0.1504px;
}

.specs {
  display: flex;
  justify-content: space-between;
  padding: 12px 0;
  border-top: 1px solid #f3f4f6;
  border-bottom: 1px solid #f3f4f6;
  margin-bottom: 16px;
}

.spec {
  display: flex;
  align-items: center;
  gap: 8px;
  font-family: 'Inter', 'Noto Sans JP', sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  color: #4a5565;
  letter-spacing: -0.1504px;
}

.host {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: auto;
}

.hostAvatar {
  width: 32px;
  height: 32px;
  background-color: #dbeafe;
  color: #155dfc;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Inter', 'Noto Sans JP', sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
  letter-spacing: -0.1504px;
  flex-shrink: 0;
}

.hostInfo {
  flex: 1;
  min-width: 0;
}

.hostName {
  font-family: 'Inter', 'Noto Sans JP', sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  color: #4a5565;
  letter-spacing: -0.1504px;
}

.hostTrips {
  font-family: 'Inter', 'Noto Sans JP', sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 16px;
  color: #99a1af;
}

.footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
}

.price {
  display: flex;
  flex-direction: column;
}

.priceValue {
  font-family: 'Inter', sans-serif;
  font-size: 24px;
  font-weight: 700;
  line-height: 32px;
  color: #155dfc;
  letter-spacing: 0.0703px;
}

.priceUnit {
  font-family: 'Inter', 'Noto Sans JP', sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 16px;
  color: #6a7282;
}

.bookButton {
  width: 111px;
  height: 44px;
  background-color: #155dfc;
  border: none;
  border-radius: 10px;
  color: #ffffff;
  font-family: 'Inter', 'Noto Sans JP', 'Noto Sans SC', sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  letter-spacing: -0.3125px;
  cursor: pointer;
  transition: background-color 0.2s;
}

.bookButton:hover {
  background-color: #134ce8;
}
